.paginator {
    margin: 10px;
    display: flex;
    flex-direction: row;
    justify-content: start;
    align-items: center;
}

.pageNumber {
    padding: 2px 5px;
    box-sizing: border-box;
    border: 1px solid #000;
    margin: 0 2px;
    border-radius: 5px;
}
.pageNumber.selectedPage {
    border-color: black;
    font-weight: bold;
}
.paginator button{
    border: none;
    background-color: rgba(123,252,142,0);
}
.paginator button img{
    width: 20px;
}
