.app-wrapper {
    margin: 0 auto;
    display: grid;
    grid-template-areas: 
    'header  header'
    'nav  content';
    grid-template-rows: 60px 1fr;
    grid-template-columns: 2fr 10fr;
    grid-gap: 10px;
}

.app-wrapper-content {
    padding: 25px;
    grid-area: content;
    background-color: rgb(195, 230, 248);
}
